<template>
	<view class="content">
		<view>
			<!-- 提示信息弹窗 -->
			<uni-popup ref="message" type="message">
				<uni-popup-message :type="msgType" :message="messageText" :duration="2000"></uni-popup-message>
			</uni-popup>
		</view>
		<view class="text-area">
			<view class="login-img">
				<view class="owl-login" :class="hideEyes?'password':''">
					<view class="hand"></view>
					<view class="hand hand-r"></view>
					<view class="arms">
						<view class="arm"></view>
						<view class="arm arm-r"></view>
					</view>
				</view>
			</view>
			<view class="textarea_from">
				<u--form :model="loginForm" ref="uLoginForm">
					<view>
						<text class="title">今日备忘</text>
						<br />
						<p style="font-weight: bold;margin-top: 8px;">{{dateFormat(date)}}</p>
					</view>
					<u-form-item prop="title">
						<u-input v-model="loginForm.title" placeholder="备忘主题" style="background-color: white" />
					</u-form-item>
					<u-form-item prop="content">
						<u--textarea v-model="loginForm.content" @focus="passwordF_B" @blur="passwordF_B" height=160
							count :maxlength="maxlength" placeholder="请输入备忘内容" border="bottom"></u--textarea>
					</u-form-item>
					<view class="btn_Box">
						<button type="primary" class="submitBtn" @click="add()">备忘新建</button>
						<button type="warn" class="resetBtn" @click="resetFrom()">备忘重置</button>
					</view>
				</u--form>
			</view>
	
		</view>
	</view>
</template>

<script>
import { mapState } from "vuex";
import moment from "moment";
	export default {
		data() {
			return {
				type: 'center',
				msgType: 'success',
				messageText: '这是一条成功提示',
				hideEyes: false,
				date: new Date().toISOString(),
				height: 290,
				maxlength: 10000,
				loginForm: {
					content: "",
					title: ""
				},
			}
		},
		computed: {
		  ...mapState("loginModule", ["userinfo"]),
		},
		mounted() {
			let _this = this;
			setInterval(function() {
				_this.date = Date.parse(new Date());
			}, 1000);
		},
		methods: {
			messageToggle(type,zxl) {
				this.msgType = type
				this.messageText = zxl
				this.$refs.message.open()
			},
			dateFormat(time) {
				let date = new Date(time);
				let year = date.getFullYear();
				// 在日期格式中，月份是从0开始的，因此要加0，使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
				let month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
				let day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
				let hours = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
				let minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
				let seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
				// 拼接
				return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
				// return year + "-" + month + "-" + day;
			},
		  async	add() {
				 if(this.loginForm.title.trim()==''){
					uni.showToast({
						icon:"none",
						title:'请填写备忘主题'
					})
				}
				else if(this.loginForm.content.trim()==''){
					uni.showToast({
						icon:"none",
						title:'请填写备忘内容'
					})
				}
				else{
			   let res=await this.$zxl('/addmemo',{
			   			content: this.loginForm.content,
			   			title: this.loginForm.title,
			   			userid: this.userinfo.id,
			   			date: moment().format("YYYY-MM-DD HH:mm:ss")
			   		},'POST')
					if (res.data.status==200) {
						this.messageToggle('success','新增成功')
						this.resetFrom()
						
					}
				}	
			},
			resetFrom() {
				this.$refs.uLoginForm.resetFields()
			},
			passwordF_B() {
				this.hideEyes = !this.hideEyes;
			},
			formSubmit(e) {
				console.log(e.detail.value);
			}
		}
	}
</script>
